// export type TCategoryCode = keyof typeof categoryRootCode;

interface ICategoryCode {
    key: string;
    value: {
        type: string,
        name: string,
        minRole: number,
        isSelectable: boolean,
    }
}
export class CategoryCode{
    //enum   
    personal = 'personal';
    common = 'common';
    adminPersonal = 'adminPersonal';

    codes: ICategoryCode[] = [
        {
            key: 'personal',
            value: {
                type: 'personal',
                name: ' ',
                minRole: 100,
                isSelectable: true,
            }
        },
        {
            key: 'common',
            value: {
                type: 'common',
                name: ' ',
                minRole: 200,
                isSelectable: true,
            }
        },
        {
            key: 'adminPersonal',
            value: {
                type: 'personal',
                name: ' ',
                minRole: 200,
                isSelectable: false,
            },
        },
    ]
    //  
    getByRoles(roleId: number){
        return this.codes.filter(d => d.value.minRole <= roleId)
    }

    //  
    getSelectables(){
        return this.codes.filter(d => d.value.isSelectable === true);
    }
}